/* –°—Ç–∏–ª—ñ –¥–ª—è —Å–µ–∫—Ü—ñ—ó "–ü—Ä–æ –ù–∞—Å" */
.section-about-us {
    padding: 20px 0;
    background-color: #f9f9f9;
    text-align: center;
}

.section-about-us h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 2rem;
    margin-bottom: 10px;
}

.section-about-us p {
    font-size: 1.1rem;
    line-height: 1.8;
    max-width: 800px;
    margin: 0 auto;
    max-height: 100px;
}

/* –°—Ç–∏–ª—ñ –¥–ª—è —Å–µ–∫—Ü—ñ—ó "–ù–∞—à—ñ –Ω–∞–π–∫—Ä–∞—â—ñ —É—á–Ω—ñ" */
.section-top-students {
    padding: 60px 0;
    background-color: #f9f9f9; /* –°–≤—ñ—Ç–ª–∏–π —Ñ–æ–Ω –¥–ª—è –≤—Å—ñ—î—ó —Å–µ–∫—Ü—ñ—ó */
    text-align: center;
    position: relative;
    overflow: hidden;
}

.section-top-students h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 2.5rem;
    margin-bottom: 60px;
    color: #000000; /* –ß–æ—Ä–Ω–∏–π –∫–æ–ª—ñ—Ä —Ç–µ–∫—Å—Ç—É */
    position: relative;
    z-index: 2;
}

.section-top-students h2::after {
    content: '';
    position: absolute;
    bottom: -15px;
    left: 50%;
    transform: translateX(-50%);
    width: 150px;
    height: 5px;
    background-color: #ff0000;
    border-radius: 3px;
    z-index: 1;
}

.student-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 40px;
    padding: 0 20px;
    max-width: 1400px;
    margin: 0 auto;
    position: relative;
    z-index: 2;
}

/* –ß–æ—Ä–Ω–∏–π —Å—Ç–∏–ª—å –¥–ª—è –∫–∞—Ä—Ç–æ–∫ —Å—Ç—É–¥–µ–Ω—Ç—ñ–≤ */
.student-card {
    background-color: #000000; /* –ß–æ—Ä–Ω–∏–π —Ñ–æ–Ω –¥–ª—è –∫–∞—Ä—Ç–∫–∏ */
    padding: 30px;
    border-radius: 20px;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    position: relative;
    overflow: hidden;
    text-align: center;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.student-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.25);
}

/* –ó–±—ñ–ª—å—à–µ–Ω—ñ —Ñ–æ—Ç–æ–≥—Ä–∞—Ñ—ñ—ó */
.student-image {
    width: 260px; /* –ó–±—ñ–ª—å—à–µ–Ω–æ —Ä–æ–∑–º—ñ—Ä –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ –¥–ª—è –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è */
    height: 260px; /* –ó–±—ñ–ª—å—à–µ–Ω–æ —Ä–æ–∑–º—ñ—Ä –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ –¥–ª—è –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è */
    margin: 0 auto 20px;
    border-radius: 50%;
    overflow: hidden;
    border: 6px solid #0077b6; /* –°–∏–Ω—ñ–π –±–æ—Ä–¥–µ—Ä –¥–ª—è –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è */
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease, border-color 0.3s ease;
    position: relative;
    z-index: 2;
}

.student-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
}

.student-card:hover .student-image img {
    transform: scale(1.1);
}

.student-card h3 {
    font-family: 'Montserrat', sans-serif;
    font-size: 1.8rem;
    margin-bottom: 15px;
    color: #ffffff; /* –ë—ñ–ª–∏–π –∫–æ–ª—ñ—Ä —Ç–µ–∫—Å—Ç—É */
    position: relative;
    z-index: 2;
}

.student-card p {
    font-size: 1.1rem;
    color: #e0e0e0; /* –°–≤—ñ—Ç–ª–∏–π —Å—ñ—Ä–∏–π –∫–æ–ª—ñ—Ä —Ç–µ–∫—Å—Ç—É –¥–ª—è –∫—Ä–∞—â–æ—ó —á–∏—Ç–∞–±–µ–ª—å–Ω–æ—Å—Ç—ñ */
    line-height: 1.6;
    margin-bottom: 20px;
    position: relative;
    z-index: 2;
}

.student-social {
    display: flex;
    justify-content: center;
    gap: 20px;
    position: relative;
    z-index: 2;
    background-color: #1a1a1a; /* –¢–µ–º–Ω–∏–π —Ñ–æ–Ω –¥–ª—è —ñ–∫–æ–Ω–æ–∫ */
    padding: 15px;
    border-radius: 15px;
    margin-top: auto;
}

.student-social a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    color: white;
    text-decoration: none;
    border-radius: 50%;
    transition: all 0.3s ease;
    background-color: rgba(255, 255, 255, 0.1);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
}

.student-social a:hover {
    transform: scale(1.1);
}

/* –ö–æ–ª—å–æ—Ä–∏ –¥–ª—è –∫–æ–∂–Ω–æ—ó —ñ–∫–æ–Ω–∫–∏ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
.student-social .social-link1:hover {
    background: #f09433;
    background: -moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
    background: -webkit-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
    background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
}

.student-social .social-link2:hover {
    background: #1877f2;
    background: -moz-linear-gradient(45deg, #1877f2 0%, #1e90ff 50%, #00bfff 100%);
    background: -webkit-linear-gradient(45deg, #1877f2 0%, #1e90ff 50%, #00bfff 100%);
    background: linear-gradient(45deg, #1877f2 0%, #1e90ff 50%, #00bfff 100%);
}

.student-social .social-link3:hover {
    background: #000000;
    background: -moz-linear-gradient(45deg, #000000 0%, #ff0050 50%, #ff0000 100%);
    background: -webkit-linear-gradient(45deg, #000000 0%, #ff0050 50%, #ff0000 100%);
    background: linear-gradient(45deg, #000000 0%, #ff0050 50%, #ff0000 100%);
}

.student-social .social-link4:hover {
    background: #0088cc;
    background: -moz-linear-gradient(45deg, #0088cc 0%, #00aced 50%, #00bfff 100%);
    background: -webkit-linear-gradient(45deg, #0088cc 0%, #00aced 50%, #00bfff 100%);
    background: linear-gradient(45deg, #0088cc 0%, #00aced 50%, #00bfff 100%);
}

/* –ù–æ–≤–∞ –∞–Ω—ñ–º–∞—Ü—ñ—è: —Ö–≤–∏–ª—è */
@keyframes wave {
    0% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(1.5);
        opacity: 0.5;
    }
    100% {
        transform: scale(2);
        opacity: 0;
    }
}

/* –ï—Ñ–µ–∫—Ç —Ö–≤–∏–ª—ñ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
.student-social a:hover::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    animation: wave 0.8s ease-out;
    z-index: 1;
}

/* –î–æ–¥–∞—Ç–∫–æ–≤–∏–π –µ—Ñ–µ–∫—Ç —Ç—ñ–Ω—ñ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
.student-social a:hover {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.3);
}

/* –°—Ç–∏–ª—ñ –¥–ª—è –∑–æ–±—Ä–∞–∂–µ–Ω—å —ñ–∫–æ–Ω–æ–∫ */
.student-social img {
    width: 30px;
    height: 30px;
    filter: brightness(0) invert(1);
    transition: filter 0.3s ease;
}

.student-social .social-link1 img {
    width: 40px;
    height: 40px;
}

.student-social .social-link2 img {
    width: 35px;
    height: 35px;
}

/* –ó–º—ñ–Ω–∞ –∫–æ–ª—å–æ—Ä—É —ñ–∫–æ–Ω–æ–∫ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
.student-social .social-link1:hover img {
    filter: brightness(1) invert(0);
}

.student-social .social-link2:hover img {
    filter: brightness(1) invert(0);
}

.student-social .social-link4:hover img {
    filter: brightness(1) invert(0);
}