/* –°—ñ—Ç–∫–∞ –∫—É—Ä—Å—ñ–≤ */
.course-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 25px;
    padding: 20px;
}

/* –ö–∞—Ä—Ç–∫–∞ –∫—É—Ä—Å—É */
.course-card {
    background-color: #ffffff; /* –ë—ñ–ª–∏–π —Ñ–æ–Ω */
    padding: 25px;
    border-radius: 15px;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease;
    border: 1px solid #e0e0e0;
    position: relative;
    overflow: hidden;
}

/* –ï—Ñ–µ–∫—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ –Ω–∞ –∫–∞—Ä—Ç–∫—É */
.course-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
}

/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ –∫–∞—Ä—Ç–∫–∏ */
.course-card h3 {
    font-family: 'Montserrat', sans-serif;
    font-size: 1.4rem;
    color: #000000; /* –ß–æ—Ä–Ω–∏–π –∫–æ–ª—ñ—Ä –¥–ª—è –∑–∞–≥–æ–ª–æ–≤–∫—ñ–≤ */
    margin-top: 0;
    margin-bottom: 20px;
    position: relative;
    z-index: 2;
}

/* –ü—ñ–¥–∫—Ä–µ—Å–ª–µ–Ω–Ω—è –∑–∞–≥–æ–ª–æ–≤–∫–∞ */
.course-card h3::after {
    content: '';
    display: block;
    width: 60px;
    height: 3px;
    background-color: #ff0000; /* –ß–µ—Ä–≤–æ–Ω–∞ –ª—ñ–Ω—ñ—è */
    margin: 10px 0;
    transition: width 0.3s ease;
}

.course-card:hover h3::after {
    width: 100px; /* –ó–±—ñ–ª—å—à—É—î–º–æ –ª—ñ–Ω—ñ—é –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
}

/* –¢–µ–∫—Å—Ç –∫–∞—Ä—Ç–∫–∏ */
.course-card p {
    margin: 15px 0;
    color: #555555;
    font-size: 1rem;
    line-height: 1.6;
    position: relative;
    z-index: 2;
}

/* –ö–Ω–æ–ø–∫–∏ */
.course-card button {
    align-items: center;
    background-color: #ffffff; /* –ë—ñ–ª–∏–π —Ñ–æ–Ω –∫–Ω–æ–ø–æ–∫ */
    border: 2px solid #000000; /* –ß–æ—Ä–Ω–∞ —Ä–∞–º–∫–∞ */
    border-radius: 8px;
    color: #000000; /* –ß–æ—Ä–Ω–∏–π —Ç–µ–∫—Å—Ç */
    cursor: pointer;
    display: inline-flex;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 500;
    height: 40px;
    justify-content: center;
    padding: 0 20px;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease;
    margin: 5px;
    position: relative;
    z-index: 2;
}

.course-card button:hover {
    background-color: #000000; /* –ß–æ—Ä–Ω–∏–π —Ñ–æ–Ω –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
    color: #ffffff; /* –ë—ñ–ª–∏–π —Ç–µ–∫—Å—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
    transform: translateY(-2px);
}

.course-card button:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2); /* –¢—ñ–Ω—å –¥–ª—è —Ñ–æ–∫—É—Å—É */
}

.course-card button:active {
    transform: translateY(0);
}

/* –°—Ç–∏–ª—ñ –¥–ª—è –∫–Ω–æ–ø–∫–∏ "–†–æ–∑–ø–æ—á–∞—Ç–∏ –∫—É—Ä—Å" */
.course-card .start-course {
    background-color: #0077b6; /* –ì–æ–ª—É–±–∏–π */
    color: #ffffff; /* –ë—ñ–ª–∏–π —Ç–µ–∫—Å—Ç */
    border: 2px solid #0077b6; /* –ì–æ–ª—É–±–∞ —Ä–∞–º–∫–∞ */
}

.course-card .start-course:hover {
    background-color: #023e8a; /* –¢–µ–º–Ω–∏–π –≥–æ–ª—É–±–∏–π */
    border-color: #023e8a;
}

/* –°—Ç–∏–ª—ñ –¥–ª—è –∫–Ω–æ–ø–∫–∏ "–ö—É—Ä—Å –∑–∞–≤–µ—Ä—à–µ–Ω–æ" */
.course-card .complete-course {
    background-color: #d90429; /* –ß–µ—Ä–≤–æ–Ω–∏–π */
    color: #ffffff; /* –ë—ñ–ª–∏–π —Ç–µ–∫—Å—Ç */
    border: 2px solid #d90429; /* –ß–µ—Ä–≤–æ–Ω–∞ —Ä–∞–º–∫–∞ */
}

.course-card .complete-course:hover {
    background-color: #9d0208; /* –¢–µ–º–Ω–∏–π —á–µ—Ä–≤–æ–Ω–∏–π */
    border-color: #9d0208;
}

/* –°—Ç–∏–ª—ñ –¥–ª—è –∫—É—Ä—Å—É "–í –ø—Ä–æ—Ü–µ—Å—ñ" */
.course-card.in-progress {
    background-color: #caf0f8; /* –°–≤—ñ—Ç–ª–æ-–≥–æ–ª—É–±–∏–π */
    position: relative;
}

.course-card.in-progress::after {
    content: "–í –ø—Ä–æ—Ü–µ—Å—ñ";
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #0077b6; /* –ì–æ–ª—É–±–∏–π */
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 12px;
    font-weight: 500;
    z-index: 2;
}

/* –°—Ç–∏–ª—ñ –¥–ª—è –∑–∞–≤–µ—Ä—à–µ–Ω–æ–≥–æ –∫—É—Ä—Å—É */
.course-card.completed {
    background-color: #ffccd5; /* –°–≤—ñ—Ç–ª–æ-—á–µ—Ä–≤–æ–Ω–∏–π */
}

.course-card.completed::after {
    content: "–ó–∞–≤–µ—Ä—à–µ–Ω–æ";
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #d90429; /* –ß–µ—Ä–≤–æ–Ω–∏–π */
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 12px;
    font-weight: 500;
    z-index: 2;
}

/* –î–æ–¥–∞—î–º–æ –≥—Ä–∞–¥—ñ—î–Ω—Ç–Ω–∏–π –µ—Ñ–µ–∫—Ç –Ω–∞ —Ñ–æ–Ω –∫–∞—Ä—Ç–∫–∏ */
.course-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, rgba(255, 0, 0, 0.05), rgba(0, 0, 255, 0.05));
    z-index: 1;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.course-card:hover::before {
    opacity: 1; /* –ü–æ–∫–∞–∑—É—î–º–æ –≥—Ä–∞–¥—ñ—î–Ω—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
}

.cta-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #000000;
    color: white;
    text-decoration: none;
    border-radius: 5px;
    font-weight: bold;
    text-align: center;
    transition: background-color 0.3s ease;
    position: relative;
    z-index: 10;
}

.cta-button:hover {
    background-color: #ff0000;
}

/* –°—Ç–∏–ª—ñ –¥–ª—è —Ñ—ñ–ª—å—Ç—Ä—ñ–≤ */
.filters {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    padding: 0 20px;
}

.filters select {
    padding: 10px;
    border-radius: 8px;
    border: 2px solid #000000;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    cursor: pointer;
}

.filters select:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2);
}

/* –°—Ç–∏–ª—ñ –¥–ª—è –∫–Ω–æ–ø–∫–∏ —Å–æ—Ä—Ç—É–≤–∞–Ω–Ω—è */
.sort-button {
    padding: 10px 20px;
    background-color: #0077b6;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.sort-button:hover {
    background-color: #023e8a;
}

.sort-button:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2);
}