/* –§—É—Ç–µ—Ä */
footer {
    background-color: #000000;
    color: #ffffff;
    text-align: center;
    padding: 40px 0;
    margin-top: 60px;
    border-top: 2px solid #ff0000; /* –ß–µ—Ä–≤–æ–Ω–∞ –ª—ñ–Ω—ñ—è –∑–≤–µ—Ä—Ö—É */
    position: relative;
    overflow: hidden;
}

/* –î–æ–¥–∞—î–º–æ –≥—Ä–∞–¥—ñ—î–Ω—Ç–Ω–∏–π –µ—Ñ–µ–∫—Ç –Ω–∞ —Ñ–æ–Ω */
footer::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, rgba(255, 0, 0, 0.1), rgba(0, 0, 255, 0.1));
    z-index: 1;
    pointer-events: none;
}

/* –°—Ç–∏–ª—ñ–∑–∞—Ü—ñ—è —Ç–µ–∫—Å—Ç—É */
footer p {
    font-size: 18px;
    margin: 0;
    position: relative;
    z-index: 2;
    font-family: 'Arial', sans-serif;
    letter-spacing: 1px;
    color: #ffffff;
}

/* –î–æ–¥–∞—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é –¥–ª—è —Ç–µ–∫—Å—Ç—É */
footer p:hover {
    color: #ff0000; /* –ß–µ—Ä–≤–æ–Ω–∏–π –∫–æ–ª—ñ—Ä –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
    transition: color 0.3s ease;
}

/* –î–æ–¥–∞—î–º–æ —ñ–∫–æ–Ω–∫–∏ —Å–æ—Ü—ñ–∞–ª—å–Ω–∏—Ö –º–µ—Ä–µ–∂ (–ø—Ä–∏–∫–ª–∞–¥) */
footer .social-icons {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 15px;
}

footer .social-icons a {
    color: #ffffff;
    font-size: 24px;
    text-decoration: none;
    transition: color 0.3s ease, transform 0.3s ease;
}

footer .social-icons a:hover {
    color: #ff0000; /* –ß–µ—Ä–≤–æ–Ω–∏–π –∫–æ–ª—ñ—Ä –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
    transform: translateY(-5px); /* –ü—ñ–¥–Ω—ñ–º–∞—î–º–æ —ñ–∫–æ–Ω–∫—É –≤–≥–æ—Ä—É */
}

/* –ê–Ω—ñ–º–∞—Ü—ñ—è –¥–ª—è —Ñ—É—Ç–µ—Ä–∞ */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

footer {
    animation: fadeInUp 1s ease-out;
}