/* –•–µ–¥–µ—Ä */
header {
    background: linear-gradient(135deg, #000000, #222222);
    color: #ffffff;
    padding: 60px 0;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
    position: relative;
    overflow: hidden;
}

/* –î–æ–¥–∞—î–º–æ –µ—Ñ–µ–∫—Ç "—Å–≤—ñ—Ç–ª–æ–≤–∏—Ö –ø—Ä–æ–º–µ–Ω—ñ–≤" */
header::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 10%, transparent 20%);
    animation: rotateLight 10s linear infinite;
    pointer-events: none;
    z-index: 1;
}

@keyframes rotateLight {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ */
header h1 {
    font-family: 'Montserrat', sans-serif;
    font-size: 3rem;
    margin: 0;
    text-align: center;
    animation: fadeIn 1.5s ease-in-out;
    text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.5);
    position: relative;
    z-index: 2;
}

/* –ü—ñ–¥–∫—Ä–µ—Å–ª–µ–Ω–Ω—è –∑–∞–≥–æ–ª–æ–≤–∫–∞ */
header h1::after {
    content: '';
    display: block;
    width: 80px;
    height: 4px;
    background-color: #ff0000; /* –ß–µ—Ä–≤–æ–Ω–∞ –ª—ñ–Ω—ñ—è */
    margin: 10px auto 0;
    animation: scaleIn 1s ease-in-out;
}

@keyframes scaleIn {
    from {
        transform: scaleX(0);
    }
    to {
        transform: scaleX(1);
    }
}

/* –ù–∞–≤—ñ–≥–∞—Ü—ñ—è */
nav ul {
    list-style: none;
    padding: 0;
    margin: 30px 0 0;
    display: flex;
    justify-content: center;
    gap: 40px;
    position: relative;
    z-index: 2;
}

nav ul li {
    margin: 0;
}

nav ul li a {
    color: #ffffff;
    text-decoration: none;
    font-weight: bold;
    font-size: 1.2rem;
    transition: color 0.4s ease, transform 0.4s ease;
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
}

/* –°—Ç–∏–ª—ñ –¥–ª—è —ñ–∫–æ–Ω–æ–∫ —É –Ω–∞–≤—ñ–≥–∞—Ü—ñ—ó */
.nav-icon {
    font-size: 2.5rem; /* –í–µ–ª–∏–∫—ñ —ñ–∫–æ–Ω–∫–∏ */
    color: #ffffff;
    transition: all 0.4s ease;
    filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.7)); /* –¢—ñ–Ω—å –¥–ª—è —ñ–∫–æ–Ω–æ–∫ */
}

/* –ï—Ñ–µ–∫—Ç–∏ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ –Ω–∞ —ñ–∫–æ–Ω–∫–∏ */
nav ul li a:hover .nav-icon {
    color: #ff0000; /* –ß–µ—Ä–≤–æ–Ω–∏–π –∫–æ–ª—ñ—Ä —ñ–∫–æ–Ω–∫–∏ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
    transform: scale(1.2); /* –ó–±—ñ–ª—å—à–µ–Ω–Ω—è —ñ–∫–æ–Ω–∫–∏ */
    filter: drop-shadow(0 0 10px rgba(255, 0, 0, 0.8)); /* –ß–µ—Ä–≤–æ–Ω–∞ —Ç—ñ–Ω—å */
}

/* –°—Ç–∏–ª—ñ –¥–ª—è —ñ–∫–æ–Ω–æ–∫ —Å–æ—Ü—ñ–∞–ª—å–Ω–∏—Ö –º–µ—Ä–µ–∂ */
.social-icons {
    display: flex;
    gap: 15px;
    position: absolute;
    right: 20px;
    top: 15%;
    transform: translateY(-50%);
    z-index: 2;
}

.social-icon {
    font-size: 2.5rem; /* –í–µ–ª–∏–∫—ñ —ñ–∫–æ–Ω–∫–∏ */
    color: #ffffff;
    transition: all 0.3s ease;
    filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.7)); /* –¢—ñ–Ω—å –¥–ª—è —ñ–∫–æ–Ω–æ–∫ */
}

.social-icons a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px; /* –†–æ–∑–º—ñ—Ä –∫–Ω–æ–ø–∫–∏ */
    height: 60px; /* –†–æ–∑–º—ñ—Ä –∫–Ω–æ–ø–∫–∏ */
    color: white;
    text-decoration: none;
    border-radius: 50%;
    transition: all 0.3s ease;
    background-color: rgba(255, 255, 255, 0.1);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* –¢—ñ–Ω—å –¥–ª—è –∫—Ä–∞—â–æ–≥–æ –≤–∏–≥–ª—è–¥—É */
    position: relative;
    overflow: hidden; /* –©–æ–± —Ö–≤–∏–ª—è –Ω–µ –≤–∏—Ö–æ–¥–∏–ª–∞ –∑–∞ –º–µ–∂—ñ */
}

.social-icons a:hover {
    transform: scale(1.1);
}

/* –ö–æ–ª—å–æ—Ä–∏ –¥–ª—è –∫–æ–∂–Ω–æ—ó —ñ–∫–æ–Ω–∫–∏ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
.social-link1:hover {
    background: #f09433;
    background: -moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
    background: -webkit-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
    background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
}

.social-link2:hover {
    background: #1877f2;
    background: -moz-linear-gradient(45deg, #1877f2 0%, #1e90ff 50%, #00bfff 100%);
    background: -webkit-linear-gradient(45deg, #1877f2 0%, #1e90ff 50%, #00bfff 100%);
    background: linear-gradient(45deg, #1877f2 0%, #1e90ff 50%, #00bfff 100%);
}

/* –ì—Ä–∞–¥—ñ—î–Ω—Ç –¥–ª—è TikTok */
.social-link3:hover {
    background: #000000;
    background: -moz-linear-gradient(45deg, #000000 0%, #ff0050 50%, #ff0000 100%);
    background: -webkit-linear-gradient(45deg, #000000 0%, #ff0050 50%, #ff0000 100%);
    background: linear-gradient(45deg, #000000 0%, #ff0050 50%, #ff0000 100%);
}

/* –ì—Ä–∞–¥—ñ—î–Ω—Ç –¥–ª—è Telegram */
.social-link4:hover {
    background: #0088cc;
    background: -moz-linear-gradient(45deg, #0088cc 0%, #00aced 50%, #00bfff 100%);
    background: -webkit-linear-gradient(45deg, #0088cc 0%, #00aced 50%, #00bfff 100%);
    background: linear-gradient(45deg, #0088cc 0%, #00aced 50%, #00bfff 100%);
}

/* –ù–æ–≤–∞ –∞–Ω—ñ–º–∞—Ü—ñ—è: —Ö–≤–∏–ª—è */
@keyframes wave {
    0% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(1.5);
        opacity: 0.5;
    }
    100% {
        transform: scale(2);
        opacity: 0;
    }
}

/* –ï—Ñ–µ–∫—Ç —Ö–≤–∏–ª—ñ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
.social-icons a:hover::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    animation: wave 0.8s ease-out;
    z-index: 1;
}

/* –î–æ–¥–∞—Ç–∫–æ–≤–∏–π –µ—Ñ–µ–∫—Ç —Ç—ñ–Ω—ñ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ */
.social-icons a:hover {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.3);
}

/* –°—Ç–∏–ª—ñ –¥–ª—è –ª–æ–≥–æ—Ç–∏–ø—É */
.logo-link {
    display: inline-block;
    text-decoration: none;
    position: absolute;
    left: 120px;
    top: 30%;
    transform: translateY(-50%);
    z-index: 15;
    padding: 30px;
    margin: 30px;
    border: none;
    overflow: hidden;
}

.logo {
    height: 150px;
    width: auto;
    display: block;
    transition: transform 0.3s ease, filter 0.3s ease; /* –ü–ª–∞–≤–Ω–∏–π –ø–µ—Ä–µ—Ö—ñ–¥ –¥–ª—è –∑–±—ñ–ª—å—à–µ–Ω–Ω—è —Ç–∞ –ø—ñ–¥—Å–≤—ñ—Ç–∫–∏ */
}

/* –ï—Ñ–µ–∫—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ –Ω–∞ –ª–æ–≥–æ—Ç–∏–ø */
.logo-link:hover .logo {
    transform: scale(1.1); /* –õ–µ–≥–∫–µ –∑–±—ñ–ª—å—à–µ–Ω–Ω—è –ª–æ–≥–æ—Ç–∏–ø—É */
    filter: drop-shadow(0 0 10px rgba(255, 0, 0, 0.8)); /* –ß–µ—Ä–≤–æ–Ω–∞ –ø—ñ–¥—Å–≤—ñ—Ç–∫–∞ */
}